<template>
    <div class="com-container">
            <div class="top">
               <el-row>
                    <el-col :span='2' class="imag">
                        <!-- <img src="./images/u41.png"  alt=""> -->
                    </el-col>
                    <el-col :span='8' class="e-center">
                        <h2>股权交易</h2>
                        <p class="chip">流通股占比：20.72%</p>
                        <el-progress :text-inside="true" :stroke-width="15" :percentage="20.72" :show-text='false'></el-progress>
                    </el-col>
                    <el-col :span='8'>
                        <div>
                            <span class="left">总股数</span>
                            <span class="right">10000 份</span>
                        </div>
                        <div>
                            <span class="left">流通股</span>
                            <span class="right">2072  份</span>
                        </div>
                        <div>
                            <span class="left">非流通股</span>
                            <span class="right">7928  份</span>
                        </div>
                    </el-col>
                </el-row>
            </div>

            <div class="center">
                <el-form :inline="true" :model="form">
                    <el-form-item >
                        <el-input v-model.trim="form.account" placeholder="请输入股东账户地址" style="width:250px;"></el-input>
                    </el-form-item>
                    <el-form-item >
                        <el-button type="primary" @click="onSearch">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>

            <div class="review-content padding-default">
                <div class="table-box">
                    <table>
                        <thead>
                            <tr>
                                <th>序号</th>
                                <th class="special">股东账户地址</th>
                                <th class="special">账户余额(份)</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item,index) in info">
                                <td>{{ index + 1 }}</td>
                                <td class="special" :title='item.buyAddr'>{{ item.buyAddr }}</td>
                                <td class="special" :title='item.amount'>{{ item.amount }}</td>
                            </tr>
                        </tbody>
                        <!-- <tbody>
                            <tr>
                                <td>1</td>
                                <td class="special">1tAmJ6RqDyg2C7ALojc3F4K9xgnEmRwLc1tAmJ6RqDyg2C7ALojc3F4K9xgnEmRwLc1tAmJ6RqDyg2C7ALojc3F4K9xgnEmRwLc</td>
                                <td class="special">1tAmJ6RqDyg2C7ALojc3F4K9xgnEmRwLc1tAmJ6RqDyg2C7ALojc3F4K9xgnEmRwLc1tAmJ6RqDyg2C7ALojc3F4K9xgnEmRwLc</td>
                            </tr>
                        </tbody> -->
                    </table>
                </div>
                <div class="pagination-box">
                    <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage"  :page-size="pageSize" layout=" prev, pager, next,total" :total="pageTotal">
                    </el-pagination>
                </div>
            </div>


    </div>
</template>

<script>
    //import  from ''
    import ContractServies from '@/services/contract-servies';
    import ContractConfig from '@/config/contract-config';
    export default {
        //组件名
        name: 'index',
        //实例的数据对象
        data() {
            return {
                form:{
                    account:"",
                },
                info:[

                ],
                loading:false,
                //默认当前页
                currentPage:1,
                pageSize:10,
                pageTotal:0,
            }
        },
        //数组或对象，用于接收来自父组件的数据
        props: {

        },
        //计算
        computed: {

        },
        //方法
        methods: {
            onSearch(){
                console.log(111)
                var response = ContractServies.call('JuzixTokenManager','listAllBuyBalance',['',this.currentPage - 1,this.pageSize]);
                console.log("账户余额信息>>>",response);
                if(response.ret == 0){
                    var data = response.data;
                    this.info = data.items;
                    this.pageTotal = data.total;
                }
            },
            handleCurrentChange(val){
                console.log(`当前页${val}页`,this.currentPage);
                //调取提交函数
                this.onSearch();
            },

        },
        //生命周期函数
        created() {
            this.onSearch();
        },
        beforeMount() {

        },
        mounted() {

        },
        //监视
        watch: {

        },
        //组件
        components: {

        },
        //过滤器
        filters:{

        },
        //自定义指令
        directive:{

        }
    }
</script>

<!--
	作者：liangyanxiangde@163.com
	时间：2017-03-27
	描述：统一使用less,局部样式
-->
<style lang="less" scoped>
    @import "../../less/modules/color.less";
@import "../../less/modules/table.less";
@color:#eeeced;
@height:130px;
@border:#e7e7e7;
.home {
    background: #fff;
}
.com-container {

    min-height: 600px;
    overflow-x:hidden;
    background-color:#ffffff;
}
.top{
    width: 100%;
    // height:@height;
    border-bottom:1px solid @border;
    // background-color:@color;
    padding:30px 0 16px 30px;
    .el-row{
        height:100%;
    }
    .el-col{

    }
    .imag{
        height:100px;
        background: url(images/u41.png) no-repeat center;
        background-size:contain;
    }

    .e-center{
        margin-left:20px;
        margin-right:272px;

        .chip{
            // margin-top:15px;
            color:#666;
        }
    }
    h2{
        margin-top:10px;
        font-size:20px;
        color:#000;
    }
    p{
        height:20px;
        line-height: 30px;

    }
    div{
        // height:30px;

       span{
            line-height: 30px;
            &:first-child{
                color:#b4b4b4;
                margin-right:5px;
            }
        }
    }
    .el-progress{
        margin-top:15px;
    }
}
// .con{
//     background-color:#fff;
//     border:1px solid @border;
//     border-radius:5px;
//     margin:26px 26px;
// }
.center{
    // height:100px;
    padding:30px 50px 10px;
}
.review-content {
    padding:0 50px;
    .table-box{
        min-height:300px;
    }

}
.pagination-box {
    padding: 25px 0 95px;
    .el-pagination {
        float: right;
    }
}
.right{
    display: inline-block;
    width:60px;
    margin-left:20px;
    text-align: left;
}
.left{
    display: inline-block;
    width:60px;
}
tbody{
    tr{
        .special{

            width:200px;
            white-space:nowrap;
            word-break:break-all;
             word-wrap:break-word;
            overflow:hidden;
            text-overflow:ellipsis;
          }
    }
}
th,td{
    width:40%;
    padding:0 10px;
    &:first-child{
        width:20%;
    }
}
</style>


